import React from 'react';
import Button from '@gsc-wallet/popup/src/components/Button';
import Step from '@gsc-wallet/popup/src/components/Step';
import Input from '@gsc-wallet/popup/src/components/Input';
import InputCriteria from '@gsc-wallet/popup/src/components/InputCriteria';

import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import { BUTTON_TYPE, VALIDATION_STATE } from '@gsc-wallet/lib/constants';

import './css.scss';

class AccountName extends React.Component {
    state = {
        name: '',
        isValid: VALIDATION_STATE.NONE,
        hasLength: false,
        isAlphanumeric: false,
        isUnique: false,
        showCriteria: false
    };

    constructor() {
        super();

        this.onChange = this.onChange.bind(this);
    }

    onChange(name) {
        const { accounts } = this.props;
        const trimmed = name.replace(/\s{2,}/g, ' ');
        const showCriteria = trimmed.length === 0 ? false : true;
        const state = {
            name: '',
            isValid: VALIDATION_STATE.NONE,
            hasLength: false,
            isAlphanumeric: false,
            isUnique: false,
            showCriteria
        };

        if(/^\s$/.test(name) || !trimmed.length)
            return this.setState(state);

        if(trimmed.trim().length >= 4)
            state.hasLength = true;

        if(/^([A-Za-z\d\s])+$/.test(trimmed))
            state.isAlphanumeric = true;

        if(!Object.values(accounts).some(({ name }) => name === trimmed.trim()))
            state.isUnique = true;

        if(state.hasLength && state.isAlphanumeric && state.isUnique)
            state.isValid = VALIDATION_STATE.VALID;
        else state.isValid = VALIDATION_STATE.INVALID;

        state.name = trimmed;
        this.setState(state);
    }

    render() {
        const {
            onCancel = () => {},
            onSubmit = () => {}
        } = this.props;

        const {
            name,
            isValid,
            hasLength,
            isAlphanumeric,
            isUnique,
            showCriteria
        } = this.state;

        const isNameValid = isValid === VALIDATION_STATE.VALID;

        return (
            <div className='insetContainer privateKeyImport'>
                <div className='pageHeader'>
                    <div className='back' onClick={ onCancel }></div>
                    <FormattedMessage id='BUTTON.CREATE_WALLET' />
                </div>
                <div className='greyModal'>
                    <div className='step'>
                        <Step num={ 1 }></Step>
                    </div>
                    <div className='stepText'>
                        <FormattedMessage id='CREATE_ACCOUNT.STEP_1' />
                    </div>
                    <div className='pageHeaderLogoWrap hasBottomMargin'>
                        <div className='logo1'></div>
                        <div className='logoName'>GSC Wallet</div>
                    </div>
                    <div className='inputGroup hasBottomMargin'>
                        <Input
                            className='accountName'
                            placeholder='INPUT.ACCOUNT_NAME'
                            status={ isValid }
                            value={ name }
                            onChange={ this.onChange }
                            onEnter={ () => isNameValid && onSubmit(name) }
                            tabIndex={ 1 }
                        />
                        {
                            showCriteria ?
                                <div className='criteria'>
                                    <InputCriteria id='CREATION_CRITERIA.HAS_LENGTH' isValid={ hasLength } />
                                    <InputCriteria id='CREATION_CRITERIA.IS_ALPHANUMERIC' isValid={ isAlphanumeric } />
                                    <InputCriteria id='CREATION_CRITERIA.IS_UNIQUE' isValid={ isUnique } />
                                </div>
                                :
                                null
                        }
                    </div>
                    <div className='BTN'>
                        <Button
                            id='BUTTON.CONTINUE'
                            type={ BUTTON_TYPE.BLUE }
                            isValid={ isNameValid }
                            onClick={ () => isNameValid && onSubmit(name) }
                            tabIndex={ 2 }
                        />
                    </div>
                </div>
            </div>
        );
    }
}

export default connect(state => ({
    accounts: state.accounts.accounts
}))(AccountName);
