import React from 'react';
import WalletOption from '@gsc-wallet/popup/src/components/WalletOption';

import { FormattedMessage } from 'react-intl';
import { APP_STATE } from '@gsc-wallet/lib/constants';
import { PopupAPI } from '@gsc-wallet/lib/api';

const onCreationSelect = () => PopupAPI.changeState(APP_STATE.CREATING);
const onRestoreSelect = () => PopupAPI.changeState(APP_STATE.RESTORING);

const WalletCreationController = () => (
    <div className='insetContainer createOrImportWallet'>
        <div className='setLanguage'></div>
        <div className='pageHeader hasBottomMargin'>
            <div className='pageHeaderLogoWrap'>
                <div className='logo1'></div>
                <div className='logoTitle'><FormattedMessage id='CREATE_ACCOUNT_PRE.TITLE' /></div>
                <div className='logotext'>
                    <FormattedMessage id='CREATE_ACCOUNT_PRE.PHRASE' />
                </div>
            </div>
        </div>
        <div className='height60px'></div>
        <div className='gscModal'>
            <div className='walletOptions'>
                <WalletOption tabIndex={ 1 } name='CREATION.CREATE' onClick={ onCreationSelect } />
                <WalletOption tabIndex={ 2 } name='CREATION.RESTORE' onClick={ onRestoreSelect } />
            </div>
        </div>
        <div className='logoinfo'>
            <FormattedMessage id='CREATE_ACCOUNT_PRE.TERMS' />
            <a href='https://docs.google.com/document/d/1MWiFEE9OyLV3b7GIdDzL6_7CmPwhrtATSuHJy55ExLQ/edit' target='_blank'>
                <FormattedMessage id='CREATE_ACCOUNT_PRE.PRO' />
            </a>
        </div>
    </div>
);

export default WalletCreationController;
