import React from 'react';

import { FormattedMessage } from 'react-intl';

import './css.scss';

const Step = props => {
    const {
        num = 1
    } = props;

    return (
        <div className='main'>
            <div className='num active'>1</div>
            <div className={ `line ${ num >= 2 ? 'lineActive' : ''}` } ></div>
            <div className={ `num  ${ num >= 2 ? 'active' : ''}` }>2</div>
            <div className={ `line ${ num >= 3 ? 'lineActive' : ''}` } ></div>
            <div className={ `num  ${ num >= 3 ? 'active' : ''}` }>3</div>
        </div>
    );
};

export default Step;