import React from 'react';
import { IntlProvider, FormattedMessage } from 'react-intl';
import { connect } from 'react-redux';
import { PopupAPI } from '@gsc-wallet/lib/api';

import { APP_STATE } from '@gsc-wallet/lib/constants';

import RegistrationController from '@gsc-wallet/popup/src/controllers/RegistrationController';
import LoginController from '@gsc-wallet/popup/src/controllers/LoginController';
import WalletCreationController from '@gsc-wallet/popup/src/controllers/WalletCreationController';
import CreateAccountController from '@gsc-wallet/popup/src/controllers/CreateAccountController';
import RestoreAccountController from '@gsc-wallet/popup/src/controllers/RestoreAccountController';
import PageController from '@gsc-wallet/popup/src/controllers/PageController';
import ConfirmationController from '@gsc-wallet/popup/src/controllers/ConfirmationController';
import ReceiveController from '@gsc-wallet/popup/src/controllers/ReceiveController';
import SendController from '@gsc-wallet/popup/src/controllers/SendController';
import TransactionsController from '@gsc-wallet/popup/src/controllers/TransactionsController';
import SettingController from '@gsc-wallet/popup/src/controllers/SettingController';
import AddTokenController from '@gsc-wallet/popup/src/controllers/AddTokenController';
import BankController from '@gsc-wallet/popup/src/controllers/TronBankController';
import BankRecordController from '@gsc-wallet/popup/src/controllers/BankRecordController';
import BankDetailController from '@gsc-wallet/popup/src/controllers/BankDetailController';
import BankHelplController from '@gsc-wallet/popup/src/controllers/TronBankHelp';
import IncomeRecordController from '@gsc-wallet/popup/src/controllers/IncomeRecordController';
import ActivityDetailController from '@gsc-wallet/popup/src/controllers/ActivityDetailController';
import DappListController from '@gsc-wallet/popup/src/controllers/DappListController';
import AssetManageController from '@gsc-wallet/popup/src/controllers/AssetManageController';
import TransactionDetailController from '@gsc-wallet/popup/src/controllers/TransactionDetailController';
import DappWhitelistController from '@gsc-wallet/popup/src/controllers/DappWhitelistController';

import 'antd-mobile/dist/antd-mobile.css';
import 'react-custom-scroll/dist/customScroll.css';
import 'assets/styles/global.scss';
import 'react-toast-mobile/lib/react-toast-mobile.css';

import enMessages from '@gsc-wallet/popup/src/translations/en.json';
import zhMessages from '@gsc-wallet/popup/src/translations/zh.json';
import jaMessages from '@gsc-wallet/popup/src/translations/ja.json';
class App extends React.Component {
    messages = {
        en: enMessages,
        zh: zhMessages,
        ja: jaMessages
    };

    render() {
        const { appState, accounts, prices, nodes, language, lock, version, authorizeDapps } = this.props;
        let dom = null;
        console.log('========', this.props, appState);
        switch(appState) {
            case APP_STATE.UNINITIALISED:
                dom = <RegistrationController language={language} />;
                break;
            case APP_STATE.PASSWORD_SET:
                dom = <LoginController />;
                break;
            case APP_STATE.UNLOCKED:
                dom = <WalletCreationController />;
                break;
            case APP_STATE.CREATING:
                dom = <CreateAccountController />;
                break;
            case APP_STATE.RESTORING:
                dom = <RestoreAccountController />;
                break;
            case APP_STATE.READY:
                dom = <PageController />;
                break;
            case APP_STATE.REQUESTING_CONFIRMATION:
                dom = <ConfirmationController authorizeDapps={authorizeDapps} />;
                break;
            case APP_STATE.RECEIVE:
                dom = <ReceiveController accounts={accounts} address={accounts.selected.address} />;
                break;
            case APP_STATE.SEND:
                dom = <SendController accounts={accounts} />;
                break;
            case APP_STATE.TRANSACTIONS:
                dom = <TransactionsController prices={prices} accounts={accounts} onCancel={ () => PopupAPI.changeState(APP_STATE.READY) } />;
                break;
            case APP_STATE.SETTING:
                dom = <SettingController lock={lock} version={version} language={language} prices={prices} nodes={nodes} onCancel={ () => PopupAPI.changeState(APP_STATE.READY) } />;
                break;
            case APP_STATE.ADD_TRC20_TOKEN:
                dom = <AddTokenController tokens={accounts.selected.tokens} onCancel={ () => PopupAPI.changeState(APP_STATE.READY) } />;
                break;
            case APP_STATE.GSCBANK:
                dom = <BankController accounts={accounts} language={language} />;
                break;
            case APP_STATE.GSCBANK_RECORD:
                dom = <BankRecordController accounts={accounts} />;
                break;
            case APP_STATE.GSCBANK_DETAIL:
                dom = <BankDetailController accounts={accounts} />;
                break;
            case APP_STATE.GSCBANK_HELP:
                dom = <BankHelplController />;
                break;
            case APP_STATE.USDT_INCOME_RECORD:
                dom = <IncomeRecordController prices={prices} accounts={accounts} onCancel={ () => PopupAPI.changeState(APP_STATE.TRANSACTIONS) } />;
                break;
            case APP_STATE.USDT_ACTIVITY_DETAIL:
                dom = <ActivityDetailController selectedToken={accounts.selectedToken} onCancel={ () => PopupAPI.changeState(APP_STATE.USDT_INCOME_RECORD) } />;
                break;
            case APP_STATE.DAPP_LIST:
                dom = <DappListController onCancel={ () => PopupAPI.changeState(APP_STATE.READY) } />;
                break;
            case APP_STATE.ASSET_MANAGE:
                dom = <AssetManageController selected={accounts.selected} onCancel={ () => PopupAPI.changeState(APP_STATE.READY) } />;
                break;
            case APP_STATE.TRANSACTION_DETAIL:
                dom = <TransactionDetailController selectedToken={accounts.selectedToken} selected={accounts.selected} onCancel={ () => PopupAPI.changeState(APP_STATE.TRANSACTIONS) } />;
                break;
            case APP_STATE.DAPP_WHITELIST:
                dom = <DappWhitelistController authorizeDapps={authorizeDapps} onCancel={ () => PopupAPI.changeState(APP_STATE.SETTING) } />;
                break;
            default:
                dom =
                    <div className='unsupportedState' onClick={ () => PopupAPI.resetState(APP_STATE.USDT_INCOME_RECORD) }>
                        <FormattedMessage id='ERRORS.UNSUPPORTED_STATE' values={{ appState }} />
                    </div>;
        }

        return (
            <IntlProvider locale={ language } messages={ this.messages[ language ] }>
                { dom }
            </IntlProvider>
        );
    }
}

export default connect(state => ({
    language: state.app.language,
    appState: state.app.appState,
    accounts: state.accounts,
    nodes: state.app.nodes,
    prices: state.app.prices,
    lock: state.app.setting.lock,
    version: state.app.version,
    authorizeDapps: state.app.authorizeDapps
}))(App);
