/* eslint-disable react/jsx-no-target-blank */
import React from 'react';
import { FormattedMessage } from 'react-intl';
import { Toast } from 'antd-mobile';
import { PAGES, APP_STATE } from '@gsc-wallet/lib/constants';
import { app } from '@gsc-wallet/popup/src/index';
import { PopupAPI } from '@gsc-wallet/lib/api';
import { spawn } from 'child_process';
const logo = require('@gsc-wallet/popup/src/assets/images/cool/wallet.png');
const PageLink = props => {
    const {
        active = false,
        page,
        changePage
    } = props;

    const pageKey = `PAGES.${ page }`;
    const pageIndex = PAGES[ page ];

    return (
        <FormattedMessage
            id={ pageKey }
            children={ text => (
                <div
                    className={ `pageLink ${ active ? 'active' : '' }` }
                    onClick={ () => !active && changePage(pageIndex) }
                >
                    { text }
                </div>
            ) }
        />
    );
};

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.onNodeChange = this.onNodeChange.bind(this);
        this.state = {
            nodeIndex: 0,
            //showNodeList:false,
            refresh: false
        };
    }

    componentDidMount() {
        const { nodes } = this.props;
        const ns = Object.entries(nodes.nodes);
        const nodeIndex = ns.map(([nodeId, obj], i) => { obj.index = i;return [nodeId, obj]; }).filter(([nodeId, obj]) => nodeId === nodes.selected)[ 0 ][ 1 ].index;
        this.setState({ nodeIndex });
    }

    onNodeChange(nodeId, index) {
        PopupAPI.selectNode(nodeId);
        app.getNodes();
        this.setState({ nodeIndex: index, showNodeList: !this.state.showNodeList });
    }

    render() {
        const { refresh } = this.state;
        return (
            <div className='header'>
                <div className='titleContainer'>
                    <div>
                        <img src={logo} alt=''/>
                    </div>
                    <div className='iconMain'>
                        <div className='linkWrap'>
                            <a href='https://gsc.social/' target='_blank' className='link link-home'></a>
                            <a href='https://twitter.com/gsc_socialchain' target='_blank' className='link link-twiter'></a>
                            <a href='https://t.me/gsc_official' target='_blank' className='link link-telegram'></a>
                            <a href='guangsulianGSC' target='_blank' className='link link-wechat'></a>
                        </div>
                        <div>
                            <div className='fun' onClick={() => {
                                if(!refresh) {
                                    this.setState({ refresh: true }, async() => {
                                        Toast.loading();
                                        const r = await PopupAPI.refresh();
                                        if(r) {
                                            this.setState({ refresh: false });
                                            Toast.hide();
                                        }
                                    });
                                }
                            }}
                            >
                            </div>
                            <div className='fun' onClick={ () => { PopupAPI.changeState(APP_STATE.SETTING); } }></div>
                            <div className='fun' onClick={ () => { PopupAPI.lockWallet(); } }></div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Header;
